import React, { useEffect, useState } from 'react';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';

import { makeStyles } from '@material-ui/styles';

import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { useStore } from '../../store/DataProvider';

import { defaultToolbarConfig } from '../../configs/configToolbar';
import { strings } from '../../configs/strings';

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4),
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
}));

const getEditorValue = (data) => {
  const state = data ? ContentState.createFromBlockArray(
    convertFromHTML(data).contentBlocks,
    convertFromHTML(data).entityMap
  ) : '';

  if (state) {
    return EditorState.createWithContent(state);
  } else {
    return EditorState.createEmpty();
  }
};

function Dashboard({}) {
  const classes = useStyles();
  const { state: { texts }, actions: { updateTexts }} = useStore();

  const [pages, setPages] = useState(() => {
    const data = {};
    strings.forEach(page => {
      page.strings.forEach(field => {
        if (field.type === 'editor') {
          data[field.key] = getEditorValue('');
        } else {
          data[field.key] = '';
        }
      });
    });

    return data;
  });

  const handleTextFieldChange = (key, value) => {
    setPages(prev => ({ ...prev, [key]: value }));
  };

  const handleSave = () => {
    const data = [];

    for (const key in pages) {
      if (pages.hasOwnProperty(key)) {
        if (typeof pages[key] === 'object') {
          data.push({ key, text: draftToHtml(convertToRaw(pages[key].getCurrentContent())) })
        } else {
          data.push({ key, text: pages[key] });
        }
      }
    }

    updateTexts(data);
  };

  useEffect(() => {
      const data = { ...pages };
      texts.forEach(text => {
        let field = {};
        strings.forEach(item => {
          field = item.strings.find(str => str.key === text.key) || field;
        });


          if (field.type === 'editor') {
            data[text.key] = getEditorValue(text.text);
          } else {
            data[text.key] = text.text;
          }
      });

      setPages(data);
  }, [texts.__v]);

  return (
    <div className={classes.root}>
      {strings.map((data, i) => (
        <ExpansionPanel key={i}>
          <ExpansionPanelSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography className={classes.heading}>{data.page}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <Grid container spacing={2}>
              {data.strings.map((field, index) => {
                if (field.type === 'editor') {
                  return (
                    <span key={index}>
                      <Grid item xs={12}>
                        <Typography variant='body1'>{field.label}</Typography>
                      </Grid>
                      <Grid item xs={12}>
                        <DraftEditor
                          wrapperStyle={{
                            width: '100%',
                            minHeight: 150,
                            overflow: 'scroll',
                            padding: 5,
                            border: false ? '1px solid red' : '1px solid rgba(0, 0, 0, 0.42)'
                          }}
                          editorState={pages[field.key]}
                          onEditorStateChange={editorState => handleTextFieldChange(field.key, editorState)}
                          toolbar={defaultToolbarConfig}
                        />
                      </Grid>
                    </span>
                  );
                }
                return (
                  <Grid item xs={12} key={index}>
                    <TextField
                      fullWidth
                      value={pages[field.key]}
                      onChange={e => handleTextFieldChange(field.key, e.target.value)}
                      label={field.label}
                    />
                  </Grid>
                );
              })}
              <Grid item xs={12} style={{ textAlign: 'right' }}>
                <Button onClick={handleSave} variant='contained' color='primary'> Save </Button>
              </Grid>
            </Grid>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      ))}
    </div>
  );
}

export default Dashboard;
