import React, { useState, useCallback, useEffect } from 'react';
import { ViewState, EditingState } from "@devexpress/dx-react-scheduler";
import dayjs from 'dayjs';

import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Paper from '@material-ui/core/Paper';
import Input from '@material-ui/core/Input';

import {
  Scheduler,
  WeekView,
  Appointments,
  Toolbar,
  TodayButton,
  DateNavigator,
  AppointmentTooltip,
  AppointmentForm,
  EditRecurrenceMenu,
} from '@devexpress/dx-react-scheduler-material-ui';

import { useStore } from '../../store/DataProvider';

function Form({ onFieldChange, appointmentData, ...restProps }) {
  const { state: { workers, prices } } = useStore();
  appointmentData.additional = { ...appointmentData.additional };

  return (
    <AppointmentForm.BasicLayout
      appointmentData={appointmentData}
      onFieldChange={onFieldChange}
      {...restProps}
    >
      <AppointmentForm.Label
        text="Client info"
        type="title"
      />
      <AppointmentForm.TextEditor
        value={appointmentData.additional.name}
        onChange={e => onFieldChange({ additional: { ...appointmentData.additional, name: e.target.value }})}
        placeholder="Client name"
      />
      <AppointmentForm.TextEditor
        value={appointmentData.phone}
        onValueChange={val => onFieldChange({ phone: val })}
        placeholder="Phone number"
      />
      <AppointmentForm.TextEditor
        value={appointmentData.additional.email}
        onChange={e => onFieldChange({ additional: { ...appointmentData.additional, email: e.target.value }})}
        placeholder="Client email"
      />
      <AppointmentForm.TextEditor
        type="numberEditor"
        value={appointmentData.additional.total}
        onChange={e => onFieldChange({ additional: { ...appointmentData.additional, total: e.target.value }})}
        placeholder="Total price (only numbers)"
      />
      <FormControl style={{
        width: '100%',
        marginTop: 3,
        marginBottom: 1,
      }}>
        <InputLabel id="demo-mutiple-name-label">Worker</InputLabel>
        <Select
          value={appointmentData.additional.worker || 0}
          onChange={e => onFieldChange({ additional: { ...appointmentData.additional, worker: e.target.value }})}
          input={<Input />}
        >
          <MenuItem key={0} value={0}>
            Not Matter
          </MenuItem>
          {workers.map(worker => (
            <MenuItem key={worker._id} value={worker._id}>
              {worker.name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <FormControl style={{
        width: '100%',
        marginTop: 3,
        marginBottom: 1,
      }}>
        <InputLabel id="demo-mutiple-name-label">Services</InputLabel>
        <Select
          multiple
          value={appointmentData.additional.serviceIds || []}
          onChange={e => onFieldChange({ additional: { ...appointmentData.additional, serviceIds: e.target.value }})}
          input={<Input />}
        >
          {prices.map(worker => (
            <MenuItem key={worker._id} value={worker._id}>
              {worker.title}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </AppointmentForm.BasicLayout>
  );
}

function Calendar() {

  const { state: { schedules, appointments }, actions: { getSchedules, addSchedule, deleteSchedule, updateSchedule }} = useStore();

  const [selectedDate, setSelectedDate] = useState(dayjs().format());
  const [added, setAdded] = useState({});
  const [editing, setEditing] = useState({});
  const [current, setCurrent] = useState({});

  const handleAction = useCallback(({ added: addedItem, changed, deleted }) => {
    if (addedItem) {

      const data = {
        title: addedItem.title,
        rRule: addedItem.rRule || '',
        notes: addedItem.notes || '',
        phone: addedItem.phone || '',
        startDate: addedItem.startDate,
        endDate: addedItem.endDate,
        additional: addedItem.additional
      };
      addSchedule(data).then(console.log).catch(console.log);
    }
    if (changed) {
      for (const key in changed) {
        if(changed[key].exDate) {
          deleteSchedule(key);
        } else {
          updateSchedule({ ...schedules.find(item => item._id === key), ...changed[key]});
        }
      }
    }
    if (deleted !== undefined) {
      deleteSchedule(deleted);
    }
  }, [deleteSchedule, updateSchedule, addSchedule, schedules]);

  useEffect(() => {
    getSchedules();
  }, [getSchedules]);

  return (
    <Paper>
      <Scheduler
        data={schedules.map(item => ({ ...item, id: item._id, additional: appointments.find(client => client.scheduleId === item._id) }))}
      >
        <ViewState
          currentDate={selectedDate}
          onCurrentDateChange={setSelectedDate}
        />
        <WeekView startDayHour={8} endDayHour={19} />
        <Toolbar />
        <DateNavigator />
        <TodayButton />
        <EditingState
          onCommitChanges={handleAction}
          addedAppointment={added}
          onAddedAppointmentChange={e => setAdded(i => ({ ...i, ...e}))}
          appointmentChanges={current}
          onAppointmentChangesChange={e => setCurrent(i => ({ ...i, ...e}))}
          editingAppointmentId={editing}
          onEditingAppointmentIdChange={e => setEditing(i => ({ ...i, ...e}))}
        />
        <Appointments />
        <EditRecurrenceMenu />
        <AppointmentTooltip
          showOpenButton
          showDeleteButton
        />
        <AppointmentForm
          basicLayoutComponent={Form}
        />
      </Scheduler>
    </Paper>
  );
}

export default Calendar;
