import React, {useEffect, useState, useCallback} from 'react';
import { makeStyles } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { DropzoneArea } from 'material-ui-dropzone';

import { useStore } from '../../store/DataProvider';

import CardItem from '../components/Card';

import './index.scss';
import {Checkbox} from '@material-ui/core';

const defaultSlider = {
  text: '',
  link: '',
  isSlide: true,
  image: '',
};

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  margin: {
    marginTop: theme.spacing(4),
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

function Detailed(props) {
  const classes = useStyles();
  const { state: { sliders }, actions: {
    addSlider,
    updateSlider,
    deleteSlider,
    updateMainImage,
  } } = useStore();

  const [slider, setSlider] = useState({ ...defaultSlider });
  const [errors, setErrors] = useState({});

  const handleSelect = useCallback(item => {
    setSlider(item);
  }, []);

  const handleChange = useCallback((key, value) => {
    setSlider(data => ({ ...data, [key]: value }));
  }, []);

  const handleDelete = useCallback(id => {
    deleteSlider(id);
  }, [deleteSlider]);

  const handleSave = useCallback(() => {
    let error = false;

    if (!slider.image) {
      setErrors({
        image: !slider.image,
      });
      error = true;
    }

    if (slider._id) {
      !error && updateSlider(slider);
    } else {
      if (!error) {
        addSlider(slider).then(() => {
          setSlider({ ...defaultSlider });
        });
      }
    }
  }, [updateSlider, addSlider, slider]);

  const setMainImage = useCallback((id, checked) => {
    checked && updateMainImage({ id });
  }, []);

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
      >
        <Grid item xs={12} className={classes.spacer}>
          <Typography className={classes.grow} variant='h4'>Slider Images</Typography>
          <Button onClick={handleSave} variant="contained" color="primary">Save</Button>
        </Grid>
        <Grid item xs={12}>
          <form noValidate autoComplete="off">
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={6}>
                <TextField
                  fullWidth
                  label="Text"
                  onChange={e => handleChange('text', e.target.value)}
                  value={slider.text}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  fullWidth
                  label="Link"
                  onChange={e => handleChange('link', e.target.value)}
                  value={slider.link}
                />
              </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12} md={12} lg={12}>
                <DropzoneArea
                  key={slider.image}
                  dropzoneClass={classes.dropzoneContainer}
                  showPreviewsInDropzone
                  onChange={e => handleChange('image', e[0])}
                  acceptedFiles={['image/*']}
                  initialFiles={slider.image ? [`/${slider.image}`] : []}
                  filesLimit={1}
                />
              </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              {sliders.map(item => (
                <Grid item xs={12} md={3} lg={3} key={item._id}>
                  <CardItem
                    headerAction={<Checkbox checked={!item.isSlide} onChange={(e, checked) => setMainImage(item._id, checked)}/>}
                    onDelete={() => handleDelete(item._id)}
                    onEdit={() => handleSelect(item)}
                    title={item.text}
                    description={item.link}
                    image={`/${item.image}`}
                  />
                </Grid>
              ))}
            </Grid>
          </form>
        </Grid>
      </Grid>
    </div>
  );
}

export default Detailed;
