import React, {useCallback, useState} from 'react';

import FormHelperText from '@material-ui/core/FormHelperText';
import NativeSelect from '@material-ui/core/NativeSelect';
import FormControl from '@material-ui/core/FormControl';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Modal from '@material-ui/core/Modal';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  alignTextRight: {
    textAlign: 'right',
  },
  paper: {
    position: 'absolute',
    minWidth: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  formControl: {
    minWidth: 120,
    width: '100%',
  }
}));

export default function PointList({ open, categories, addPrice, handleClose }) {
  const classes = useStyles();

  const [title, setTitle] = useState('');
  const [order, setOrder] = useState(0);
  const [price, setPrice] = useState('');
  const [duration, setDuration] = useState('');
  const [categoryId, setCategory] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});

  const handleSave = useCallback(() => {
    if (!title || !price || !duration || !categoryId || order === '') {
      setErrors({
        title: !title,
        order: order === '',
        price: !price,
        duration: !duration,
        category: !categoryId,
      });
    } else {
      setLoading(true);
      addPrice({ title, order, price, duration, categoryId }).then(() => {
        setLoading(false);
        setTitle('');
        setOrder(0);
        setPrice('');
        setDuration('');
        setCategory(null);
      });
    }
  }, [title, order, price, duration, categoryId, addPrice]);

  return (
    <Modal
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
      open={open}
      onClose={handleClose}
    >
      <Paper style={{
        top: `50%`,
        left: `50%`,
        transform: `translate(-50%, -50%)`,
      }} className={classes.paper}>
        <Typography variant='subtitle1' id="simple-modal-title">Add price</Typography>
        <Grid
          container
          spacing={2}
        >
          <Grid item xs={12} md={4}>
            <TextField
              error={errors['title']}
              helperText={errors['title'] ? "Incorrect entry" : ""}
              fullWidth
              label="Title"
              onChange={e => setTitle(e.target.value)}
              value={title}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors['order']}
              helperText={errors['order'] ? "Incorrect entry" : ""}
              fullWidth
              label="Order"
              onChange={e => setOrder(e.target.value)}
              value={order}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              error={errors['price']}
              helperText={errors['price'] ? "Incorrect entry" : ""}
              fullWidth
              label="Price"
              type="Number"
              onChange={e => setPrice(e.target.value)}
              value={price}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <TextField
              error={errors['duration']}
              helperText={errors['duration'] ? "Incorrect entry" : ""}
              fullWidth
              label="Duration"
              type="Number"
              onChange={e => setDuration(e.target.value)}
              value={duration}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor="age-native-helper">Category</InputLabel>
              <NativeSelect
                className={classes.fullWidth}
                value={categoryId}
                onChange={e => setCategory(e.target.value)}
                inputProps={{
                  name: 'Category',
                  id: 'age-native-helper',
                }}
              >
                <option value=''> </option>
                {categories.map(item => (
                  <option key={item._id} value={item._id}>{item.title}</option>
                ))}
              </NativeSelect>
              {errors['category'] && <FormHelperText>Incorrect entry</FormHelperText>}
            </FormControl>
          </Grid>
          <Grid className={classes.alignTextRight} item xs={12}>
            <Button onClick={handleSave} color='primary' variant='contained'>Save</Button>
          </Grid>
        </Grid>
      </Paper>
    </Modal>
  );
}
