import React, { useState, useCallback } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import MaterialTable from 'material-table';
import dayjs from 'dayjs';

import { makeStyles } from '@material-ui/styles';
import Chip from '@material-ui/core/Chip';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import BlockIcon from '@material-ui/icons/Block';
import DoneIcon from '@material-ui/icons/Done';

import { useStore } from '../../store/DataProvider';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  content: {
    padding: 0,
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  },
  formControl: {
    minWidth: 120,
    width: '100%',
  }
}));

function Appointments() {
  const classes = useStyles();
  const {
    state: {
      appointments,
      prices,
      workers
    },
    actions: {
      updateAppointment,
    }
  } = useStore();

  const [loading, setLoading] = useState(false);

  const handleUpdate = useCallback(data => {
    setLoading(true);
    updateAppointment(data).then(() => setLoading(false));
  }, [updateAppointment]);

  const columns = [
    {
      cellStyle: { minWidth: 150 },
      title: 'Full Name',
      field: 'name',
      render: rowData => `${rowData.name} ${rowData.lastName}`,
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Phone',
      field: 'phone',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Email',
      field: 'email',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Note',
      field: 'note',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Address',
      field: 'address',
      render: rowData => (
        <span>
          <p><b>City:</b> {rowData.city}</p>
          <p><b>Post:</b> {rowData.post}</p>
          <p><b>Address:</b> {rowData.address}</p>
          <p><b>Home number:</b> {rowData.homeNumber}</p>
          <p><b>Additional address:</b> {rowData.additionalAddress}</p>
        </span>
      ),
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Birthday',
      field: 'birthday',
      render: rowData => dayjs(rowData.birthday).format('MMMM DD, YYYY'),
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Total',
      field: 'total',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Status',
      field: 'status',
      render: rowData => <Chip
        label={rowData.status === 1 ? 'Accepted' : 'Declined'}
        clickable
        color={rowData.status === 1 ? 'primary' : 'default'}
        onClick={() => handleUpdate({ ...rowData, status: rowData.status ? 0 : 1 })}
        onDelete={() => handleUpdate({ ...rowData, status: rowData.status ? 0 : 1 })}
        deleteIcon={rowData.status === 1 ? <BlockIcon/> : <DoneIcon />}
      />
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Schedule time',
      field: 'time',
      render: rowData => dayjs(rowData.time).format('MMMM DD, YYYY HH:mm'),
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Service list',
      field: 'serviceIds',
      render: rowData => prices.filter(item => rowData.serviceIds.includes(item._id)).map(item => item.title).join(', '),
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Worker',
      field: 'worker',
      render: rowData => {
        const worker = workers.find(item => rowData.worker === item._id);

        return worker ? worker.name : '';
      },
    },
    {
      cellStyle: { minWidth: 150 },
      type: 'date',
      title: 'Created At',
      field: 'date',
      render: rowData => dayjs(rowData.time).format('MMMM DD, YYYY'),
    },
  ];

  return (
    <div className={classes.root}>
      <PerfectScrollbar>
        <MaterialTable
          isLoading={loading}
          icons={{
            Delete: CancelIcon,
            Search: SearchIcon,
            Clear: CancelIcon,
            FirstPage: FirstPageIcon,
            NextPage: NavigateNextIcon,
            LastPage: LastPageIcon,
            PreviousPage: NavigateBeforeIcon,
            ResetSearch: CancelIcon,
            SortArrow: ExpandMoreIcon,
          }}
          title="Appointments list"
          columns={columns}
          data={appointments.map(item => ({...item}))}
        />
      </PerfectScrollbar>
    </div>
  );
}

export default Appointments;
