import React, { useCallback, useState } from 'react';
import draftToHtml from 'draftjs-to-html';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';

import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Modal from '@material-ui/core/Modal';
import Grid from '@material-ui/core/Grid';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { defaultToolbarConfig } from '../../configToolbar';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  header: {
    flex: 1,
  },
  headerText: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightRegular,
  },
  spacer: {
    flexGrow: 1,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  alignTextRight: {
    textAlign: 'right',
  },
  paper: {
    position: 'absolute',
    minWidth: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function PointList({ onSubmit, data, onDelete, onUpdate }) {
  const classes = useStyles();

  const [editorState, setEditorState] = useState(EditorState.createEmpty());
  const [title, setTitle] = useState('');
  const [order, setOrder] = useState(0);
  const [errors, setErrors] = useState({});
  const [open, setOpen] = useState(false);
  const [selectedId, setSelectedId] = useState(null);

  const handleSubmit = useCallback(() => {
    let error = false;
    if (!title || order === '' || draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>') {
      setErrors({
        title: !title,
        order: true,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>',
      });
      error = true;
    }

    if (!error) {
      if (selectedId) {
        onUpdate({ _id: selectedId, title, order, description: draftToHtml(convertToRaw(editorState.getCurrentContent()))});
      } else {
        onSubmit({ title, order, description: draftToHtml(convertToRaw(editorState.getCurrentContent()))});
      }
      setOpen(false);
      setTitle('');
      setOrder(0);
      setEditorState(EditorState.createEmpty());
      setSelectedId(null);
    }
  }, [selectedId, onUpdate, onSubmit, order, title, editorState]);

  const handleDelete = useCallback((e, id) => {
    e.stopPropagation();
    onDelete(id);
  }, [onDelete]);

  const openEditPopup = useCallback((e, data) => {
    e.stopPropagation();
    const state = ContentState.createFromBlockArray(
      convertFromHTML(data.description).contentBlocks,
      convertFromHTML(data.description).entityMap
    );

    setTitle(data.title);
    setOrder(data.order);
    setEditorState(EditorState.createWithContent(state));
    setSelectedId(data._id);
    setOpen(true);
  }, []);

  return (
    <Paper className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          IconButtonProps={{
            onClick: e => {
              e.stopPropagation();
              setOpen(true);
            }
          }}
          expandIcon={<IconButton
            color="secondary"
            size="medium">
            <AddIcon />
          </IconButton>}
        >
          <Typography className={classes.headerText}>Add point</Typography>
        </ExpansionPanelSummary>
      </ExpansionPanel>
      {data && data.map(item => {
        return (
          <ExpansionPanel key={item._id}>
            <ExpansionPanelSummary
              expandIcon={<ExpandMoreIcon />}
            >
              <Typography className={classes.heading}>
                <IconButton onClick={(e) => openEditPopup(e, item)} color="secondary" size="medium"><EditIcon /></IconButton>
                <IconButton onClick={(e) => handleDelete(e, item._id)} color="secondary" size="medium"><DeleteIcon /></IconButton>
                {item.title}
              </Typography>
            </ExpansionPanelSummary>
            <ExpansionPanelDetails style={{ backgroundColor: 'aliceblue'}}>
              <div dangerouslySetInnerHTML={{ __html: item.description }} />
            </ExpansionPanelDetails>
          </ExpansionPanel>
        );
      })}
      <Modal
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
        open={open}
        onClose={() => setOpen(false)}
      >
        <Paper style={{
          top: `50%`,
          left: `50%`,
          transform: `translate(-50%, -50%)`,
        }} className={classes.paper}>
          <Typography variant='subtitle1' id="simple-modal-title">Add point</Typography>
          <Grid
            container
            spacing={2}
          >
            <Grid item xs={12}>
              <TextField
                error={errors['title']}
                helperText={errors['title'] ? "Incorrect entry" : ""}
                fullWidth
                label="Title"
                onChange={e => setTitle(e.target.value)}
                value={title}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                error={errors['order']}
                helperText={errors['order'] ? "Incorrect entry" : ""}
                fullWidth
                type='number'
                label="Order"
                onChange={e => setOrder(e.target.value)}
                value={order}
              />
            </Grid>
            <Grid item xs={12}>
              <DraftEditor
                wrapperStyle={{ border: errors['description'] ? '1px solid red' : 'none' }}
                editorStyle={{ height: 180, overflow: 'scroll', padding: 5 }}
                editorState={editorState}
                onEditorStateChange={setEditorState}
                toolbar={defaultToolbarConfig}
              />
            </Grid>
            <Grid className={classes.alignTextRight} item xs={12}>
              <Button onClick={handleSubmit} color='primary' variant='containedPrimary'>Save</Button>
            </Grid>
          </Grid>
        </Paper>
      </Modal>
    </Paper>
  );
}
