import React, { useState, useCallback } from 'react';

import { makeStyles } from '@material-ui/styles';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import CircularProgress from '@material-ui/core/CircularProgress';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import ListItem from '@material-ui/core/ListItem';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List';

import RemoveIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2, 0, 0, 0),
  },
  content: {
    marginTop: theme.spacing(2)
  },
  loading: {
    display: 'flex',
    height: '100%',
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  title: {
    margin: theme.spacing(2, 2, 2, 2),
  },
  textField: {
    padding: theme.spacing(0),
  },
  list: {
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    '&:hover': {
      backgroundColor: theme.palette.primary.light,
    }
  }
}));

const UserList = ({ addCategory, updateCategory, deleteCategory, data }) => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState(null);
  const [value, setValue] = useState('');
  const [order, setOrder] = useState(0);

  const handleAdd = useCallback(() => {
    setLoading(true);
    addCategory({ title: "New added", order }).then(() => setLoading(false));
  }, [addCategory, order]);

  const handleDelete = useCallback(id => {
    setLoading(true);
    deleteCategory(id).then(() => setLoading(false));
  }, [deleteCategory]);

  const handleUpdate = useCallback(cat => {
    setLoading(true);
    updateCategory({ ...cat, title: value, order }).then(() => {
      setLoading(false);
      setSelected(null);
      setValue('');
      setOrder(0);
    });
  }, [updateCategory, value, order]);

  if (loading) {
    return (
      <div className={classes.loading}>
        <CircularProgress />
      </div>
    );
  }

  return (
    <Paper className={classes.root}>
      <Typography variant="h4" className={classes.title}>
        Categories
        <IconButton onClick={handleAdd} edge="end" aria-label="add">
          <AddIcon />
        </IconButton>
      </Typography>
      <div className={classes.list}>
        <List component="nav">
          {data.map(item => (
            <ListItem className={classes.hover} key={item._id}>
              <ListItemText
                primaryTypographyProps={{ variant: 'body1' }}
                primary={selected === item._id ? <>
                  <TextField
                    value={value}
                    onChange={e => setValue(e.target.value)}
                  />
                  <TextField
                    value={order}
                    type='number'
                    onChange={e => setOrder(e.target.value)}
                  />
                </> : item.title}
              />
              <ListItemSecondaryAction>
                {selected === item._id ? <IconButton onClick={() => handleUpdate(item)} edge="end" aria-label="check">
                  <CheckIcon />
                </IconButton>
                : <>
                    <IconButton onClick={() => {
                      setSelected(item._id);
                      setValue(item.title);
                      setOrder(item.order);
                    }} edge="end" aria-label="edit">
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      onClick={() => handleDelete(item._id)}
                      edge="end"
                      aria-label="remove"
                    >
                      <RemoveIcon />
                    </IconButton>
                  </>
                }
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </List>
      </div>
    </Paper>
  );
};

export default UserList;
