import React, { useCallback, useState } from 'react';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ListItemText from '@material-ui/core/ListItemText';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Modal from '@material-ui/core/Modal';
import List from '@material-ui/core/List';
import Grid from '@material-ui/core/Grid';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { defaultToolbarConfig } from '../../configToolbar';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  header: {
    flex: 1,
  },
  grow: {
    flexGrow: 1,
  },
  headerText: {
    fontSize: theme.typography.pxToRem(16),
    fontWeight: theme.typography.fontWeightRegular,
  },
  spacer: {
    flexGrow: 1,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  alignTextRight: {
    textAlign: 'right',
  },
  paper: {
    position: 'absolute',
    minWidth: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function PriceList({ addPriceCategory, deletePriceCategory, addPriceCategoryData, deletePriceCategoryData, updatePriceCategoryData, data }) {
  const classes = useStyles();

  const [editorState, setEditorState] = useState(EditorState.createEmpty());
  const [title, setTitle] = useState('');
  const [order, setOrder] = useState(0);
  const [priceOrder, setPriceOrder] = useState(0);
  const [errors, setError] = useState({});
  const [price, setPrice] = useState('');
  const [selectedCategoryId, setSelectedCategoryId] = useState(null);
  const [selectedPriceId, setSelectedPriceId] = useState(null);
  const [open, setOpen] = useState(false);

  const openEditPopup = useCallback((categoryId, categoryPrice) => {
    const state = ContentState.createFromBlockArray(
      convertFromHTML(categoryPrice.description).contentBlocks,
      convertFromHTML(categoryPrice.description).entityMap
    );

    setPrice(categoryPrice.price);
    setPriceOrder(categoryPrice.order);
    setEditorState(EditorState.createWithContent(state));
    setSelectedCategoryId(categoryId);
    setSelectedPriceId(categoryPrice._id);
    setOpen(true);
  }, []);

  const handleCategorySubmit = useCallback(e => {
    e.stopPropagation();

    if (title === '' || order === '') {
      setError({ ...errors, title: title === '', order: order === '' });
    } else {
      setError({ ...errors, title: false, order: false });
      addPriceCategory({ title, order, data: [] });
      setTitle('');
      setOrder(0);
    }
  }, [addPriceCategory, errors, title, order]);

  const handleCategoryDelete = useCallback((e, id) => {
    e.stopPropagation();
    deletePriceCategory(id);
  }, [deletePriceCategory]);

  const handlePriceSubmit = useCallback(() => {
    let error = false;
    if (!price || priceOrder === '' || draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>') {
      setError({
        price: !price,
        priceOrder: priceOrder === '',
        description: draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>',
      });
      error = true;
    }

    if (!error) {
      if (selectedPriceId) {
        updatePriceCategoryData({
          categoryId: selectedCategoryId,
          _id: selectedPriceId,
          order: priceOrder,
          price,
          description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
        });
      } else {
        addPriceCategoryData({
          categoryId: selectedCategoryId,
          order: priceOrder,
          price,
          description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
        });
      }
      setOpen(false);
      setPrice('');
      setPriceOrder(0);
      setEditorState(EditorState.createEmpty());
      setSelectedCategoryId(null);
      setSelectedPriceId(null);
    }
  }, [addPriceCategoryData, updatePriceCategoryData, price, selectedCategoryId, selectedPriceId, editorState, priceOrder]);

  const handlePriceDelete = useCallback((categoryId, priceId) => {
    deletePriceCategoryData(categoryId, priceId);
  }, [deletePriceCategoryData]);

  return (
    <Paper className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={(
            <>
              <IconButton
                onClick={handleCategorySubmit}
                color="secondary"
                size="medium"
              >
                <AddIcon />
              </IconButton>
            </>
          )}
        >
          <Typography className={classes.headerText}>Add prices</Typography>
          <TextField
            error={errors['title']}
            helperText={errors['title'] ? "Incorrect entry" : ""}
            fullWidth
            label="Title"
            onClick={e => e.stopPropagation()}
            onChange={e => setTitle(e.target.value)}
            value={title}
          />
        </ExpansionPanelSummary>
      </ExpansionPanel>
      {data.map(item => (
        <ExpansionPanel key={item._id}>
          <ExpansionPanelSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography className={classes.heading}>
              <IconButton
                onClick={e => {
                  e.stopPropagation();
                  setSelectedCategoryId(item._id);
                  setOpen(true);
                }}
                color="secondary"
                size="medium">
                <AddIcon />
              </IconButton>
              <IconButton
                onClick={e => handleCategoryDelete(e, item._id)}
                color="secondary"
                size="medium">
                <DeleteIcon />
              </IconButton>
              {item.title}
            </Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <List dense className={classes.grow}>
              {item.data.map(price => (
                <ListItem key={price._id}>
                  <ListItemText
                    primary={<span dangerouslySetInnerHTML={{ __html: price.description }}/>}
                    secondary={<b>{price.price} €</b>}
                    secondaryTypographyProps={{ variant: 'body1'}}
                  />
                  <ListItemSecondaryAction>
                    <IconButton onClick={() => openEditPopup(item._id, price)} edge="end" aria-label="delete">
                      <EditIcon/>
                    </IconButton>
                    <IconButton onClick={() => handlePriceDelete(item._id, price._id)} edge="end" aria-label="delete">
                      <DeleteIcon/>
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          </ExpansionPanelDetails>
        </ExpansionPanel>
      ))}
      <Modal
        open={open}
        onClose={() => setOpen(false)}
      >
        <Paper style={{
          top: `50%`,
          left: `50%`,
          transform: `translate(-50%, -50%)`,
        }} className={classes.paper}>
          <Typography variant='subtitle1' id="simple-modal-title">Add point</Typography>
          <Grid
            container
            spacing={2}
          >
            <Grid item xs={12}>
              <TextField
                error={errors['price']}
                helperText={errors['price'] ? "Incorrect entry" : ""}
                fullWidth
                label="Price"
                onChange={e => setPrice(e.target.value)}
                value={price}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                error={errors['priceOrder']}
                helperText={errors['priceOrder'] ? "Incorrect entry" : ""}
                fullWidth
                label="Order"
                onChange={e => setPriceOrder(e.target.value)}
                value={priceOrder}
              />
            </Grid>
            <Grid item xs={12}>
              <DraftEditor
                wrapperStyle={{ border: errors['description'] ? '1px solid red' : 'none' }}
                editorStyle={{ height: 180, overflow: 'scroll', padding: 5 }}
                editorState={editorState}
                onEditorStateChange={setEditorState}
                toolbar={defaultToolbarConfig}
              />
            </Grid>
            <Grid className={classes.alignTextRight} item xs={12}>
              <Button onClick={handlePriceSubmit} color='primary' variant='containedPrimary'>Save</Button>
            </Grid>
          </Grid>
        </Paper>
      </Modal>
    </Paper>
  );
}
