import React, { useState, useCallback } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import PropTypes from 'prop-types';

import { makeStyles } from '@material-ui/styles';

import FormHelperText from '@material-ui/core/FormHelperText';
import NativeSelect from '@material-ui/core/NativeSelect';
import CardContent from '@material-ui/core/CardContent';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TextField from '@material-ui/core/TextField';
import TableRow from '@material-ui/core/TableRow';
import Table from '@material-ui/core/Table';
import Card from '@material-ui/core/Card';

import DeleteIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';


const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    overflowX: 'auto',
  },
  content: {
    padding: 0,
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  },
  formControl: {
    minWidth: 120,
    width: '100%',
  }
}));

const UsersTable = ({ data, categories, onDelete, onUpdate, ...rest }) => {

  const classes = useStyles();

  const [selected, setSelected] = useState(null);
  const [title, setTitle] = useState('');
  const [price, setPrice] = useState('');
  const [order, setOrder] = useState(0);
  const [duration, setDuration] = useState('');
  const [categoryId, setCategoryId] = useState(null);
  const [errors, setErrors] = useState({});

  const startEditing = useCallback(id => {
    const selectedItem = data.find(item => item._id === id);
    setSelected(id);
    setTitle(selectedItem.title);
    setPrice(selectedItem.price);
    setOrder(selectedItem.order);
    setDuration(selectedItem.duration);
    setCategoryId(selectedItem.categoryId);
  }, [data]);

  const handleUpdate = useCallback(() => {
    if (!title || !price || !duration || !categoryId || order === '') {
      setErrors({
        title: !title,
        price: !price,
        order: order === '',
        duration: !duration,
        categoryId: !categoryId,
      });
    } else {
      onUpdate({ _id: selected, title, price, order, duration, categoryId }).then(() => {
        setSelected(null);
        setTitle('');
        setPrice('');
        setOrder(0);
        setDuration('');
        setCategoryId(null);
      });
    }
  }, [selected, title, price, duration, order, categoryId, onUpdate]);

  const renderEditableRow = item => (
    <TableRow
      hover
      key={item._id}
    >
      <TableCell>
        <TextField
          value={title}
          onChange={e => setTitle(e.target.value)}
          helperText={errors.title ? "Incorrect entry" : ""}
          variant="outlined"
        />
      </TableCell>
      <TableCell>
        <TextField
          value={order}
          onChange={e => setOrder(e.target.value)}
          helperText={errors.order ? "Incorrect entry" : ""}
          variant="outlined"
        />
      </TableCell>
      <TableCell>
        <TextField
          value={price}
          type='number'
          onChange={e => setPrice(e.target.value)}
          helperText={errors.price ? "Incorrect entry" : ""}
          variant="outlined"
        />
      </TableCell>
      <TableCell>
        <TextField
          value={duration}
          type='number'
          onChange={e => setDuration(e.target.value)}
          helperText={errors.duration ? "Incorrect entry" : ""}
          variant="outlined"
        />
      </TableCell>
      <TableCell>
        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="category-native-helper">Category</InputLabel>
          <NativeSelect
            className={classes.fullWidth}
            value={categoryId}
            onChange={e => setCategoryId(e.target.value)}
            inputProps={{
              name: 'Category',
              id: 'category-native-helper',
            }}
          >
            {categories.map(cat => (
              <option key={cat._id} value={cat._id}>{cat.title}</option>
            ))}
          </NativeSelect>
          {errors['category'] && <FormHelperText>Incorrect entry</FormHelperText>}
        </FormControl>
      </TableCell>
      <TableCell>
        <IconButton onClick={handleUpdate}>
          <CheckIcon/>
        </IconButton>
      </TableCell>
    </TableRow>
  );

  const renderStandardRow = item => (
    <TableRow
      hover
      key={item._id}
    >
      <TableCell>
        <Typography variant="body1">{item.title}</Typography>
      </TableCell>
      <TableCell>
        <Typography variant="body1">{item.order}</Typography>
      </TableCell>
      <TableCell>
        <Typography variant="body1">{item.price}</Typography>
      </TableCell>
      <TableCell>
        <Typography variant="body1">{item.duration} min</Typography>
      </TableCell>
      <TableCell>
        <Typography variant="body1">{categories.find(cat => cat._id === item.categoryId).title}</Typography>
      </TableCell>
      <TableCell>
        <IconButton onClick={() => onDelete(item._id)}>
          <DeleteIcon/>
        </IconButton>
        <IconButton onClick={() => startEditing(item._id)}>
          <EditIcon/>
        </IconButton>
      </TableCell>
    </TableRow>
  );

  return (
    <Card
      {...rest}
      className={classes.root}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div >
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell>Title</TableCell>
                  <TableCell>Order</TableCell>
                  <TableCell>Price</TableCell>
                  <TableCell>Duration</TableCell>
                  <TableCell>Category</TableCell>
                  <TableCell>Actions</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.map(item => item._id === selected ? renderEditableRow(item) : renderStandardRow(item))}
              </TableBody>
            </Table>
          </div>
        </PerfectScrollbar>
      </CardContent>
    </Card>
  );
};

UsersTable.propTypes = {
  className: PropTypes.string,
  onUpdate: PropTypes.func.isRequired
};

export default UsersTable;
