import React, { useState, useCallback } from 'react';

import { makeStyles } from '@material-ui/styles';

import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import CircularProgress from '@material-ui/core/CircularProgress';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';

import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

import { useStore } from '../../store/DataProvider';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
  content: {
    marginTop: theme.spacing(2)
  },
  loading: {
    display: 'flex',
    height: '100%',
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  textField: {
    padding: theme.spacing(0),
  },
  list: {
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    '&:hover': {
      backgroundColor: theme.palette.primary.light,
    }
  }
}));

const Workers = () => {
  const classes = useStyles();
  const { state: { workers, prices }, actions: { addWorker, updateWorker, deleteWorker }} = useStore();

  const [selected, setSelected] = useState(null);
  const [name, setName] = useState('');
  const [serviceIds, setServiceIds] = useState(() => prices.map(item => item._id));
  const [loading, setLoading] = useState(false);

  const handleChange = useCallback(e => {
    setName(e.target.value)
  }, []);

  const handleCheck = useCallback((e, checked) => {
    if(checked) {
      setServiceIds(ids => [ ...ids, e.target.value]);
    } else {
      setServiceIds(ids => ids.filter(id => id !== e.target.value));
    }
  }, []);

  const handleSave = useCallback(() => {
    setLoading(true);

    if (selected) {
      updateWorker({ _id: selected, name, serviceIds }).then(() => {
        setSelected(null);
        setName('');
        setLoading(false);
        setServiceIds(() => prices.map(item => item._id));
      });
    } else {
      addWorker({ name, serviceIds }).then(() => {
        setSelected(null);
        setName('');
        setLoading(false);
        setServiceIds(() => prices.map(item => item._id));
      });
    }
  }, [selected, name, serviceIds, addWorker, prices, updateWorker]);

  const handleDelete = useCallback(id => {
    setLoading(false);
    deleteWorker(id).then(() => setLoading(false));
  }, [deleteWorker]);

  if (loading) {
    return (
      <div className={classes.loading}>
        <CircularProgress />
      </div>
    );
  }

  return (
    <Grid container spacing={2}>
      <Grid item xs={6}>
        <Paper className={classes.root}>
          <div className={classes.list}>
            <List component="nav">
              {workers.map(item => (
                <ListItem className={classes.hover} key={item._id}>
                  <ListItemText
                    primaryTypographyProps={{ variant: 'body1'}}
                    primary={item.name}
                    secondary={`Services: ${item.serviceIds.length}`}
                  />
                  <ListItemSecondaryAction>
                    <IconButton onClick={() => handleDelete(item._id)} edge="end" aria-label="edit">
                      <DeleteIcon />
                    </IconButton>
                    <IconButton onClick={() => {
                      setSelected(item._id);
                      setName(item.name);
                      setServiceIds(item.serviceIds);
                    }} edge="end" aria-label="edit">
                      <EditIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          </div>
        </Paper>
      </Grid>
      <Grid item xs={6}>
        <Paper className={classes.root}>
          <form>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  value={name}
                  onChange={handleChange}
                  helperText={"Incorrect entry" || ""}
                  variant="outlined"
                />
              </Grid>
              <Grid item xs={12}>
                {prices.map(item => {
                  return (
                    <FormControlLabel
                      key={item._id}
                      control={
                        <Checkbox
                          checked={serviceIds.includes(item._id)}
                          onChange={handleCheck}
                          value={item._id}
                          color="primary"
                        />
                      }
                      label={item.title}
                    />
                  );
                })}
              </Grid>
              <Grid item xs={12}>
                <Button variant="contained" onClick={handleSave}>Save</Button>
                <Button variant="contained" onClick={() => {
                  setSelected(null);
                  setName('');
                  setServiceIds(prices.map(item => item._id));
                }}>Cancel</Button>
              </Grid>
            </Grid>
          </form>
        </Paper>
      </Grid>
    </Grid>
  );
};

export default Workers;
