import React, { useState, useCallback } from 'react';
import { makeStyles } from '@material-ui/styles';

import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';

import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

import Categories from './Categories';
import Prices from './Prices';
import Modal from './Modal';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

function Configs(props) {
  const classes = useStyles();
  const {
    state: {
      prices,
      categories
    },
    actions: {
      addCategory,
      updateCategory,
      deleteCategory,
      addPrice,
      updatePrice,
      deletePrice
    }
  } = useStore();

  const [open, setOpen] = useState(false);

  const handleClose = useCallback(() => setOpen(false), []);

  const handleAdd = useCallback(data => {
    return addPrice(data).then(() => {
      setOpen(false);
    })
  }, [addPrice]);

  const handleDelete = useCallback(id => {
    return deletePrice(id).then(() => {
      setOpen(false);
    })
  }, [deletePrice]);

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
      >
        <Grid item xs={12} className={classes.spacer}>
          <Typography className={classes.grow} variant='h4'>Configs</Typography>
        </Grid>

        <Grid item xs={12}>
          <Grid
            container
            spacing={2}
          >
            <Grid item xs={12} md={4}>
              <Categories
                data={categories}
                addCategory={addCategory}
                deleteCategory={deleteCategory}
                updateCategory={updateCategory}
              />
            </Grid>
            <Grid item xs={12} md={8}>
              <Prices
                onUpdate={updatePrice}
                onDelete={handleDelete}
                data={prices}
                categories={categories}
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Modal categories={categories} open={open} addPrice={handleAdd} handleClose={handleClose}/>
      <Fab aria-label="Edit" className={classes.fab} color="secondary" onClick={() => setOpen(true)}>
        <AddIcon />
      </Fab>
    </div>
  );
}

export default Configs;
