import React, {useEffect, useState, useCallback} from 'react';
import { makeStyles } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { DropzoneArea } from 'material-ui-dropzone';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';
import draftToHtml from 'draftjs-to-html';

import { useStore } from '../../store/DataProvider';

import PointsList from './components/points';
import PriceList from './components/prices';

import { defaultToolbarConfig } from './configToolbar';

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import './index.scss';

const defaultService = {
  title: '',
  subTitle: '',
  order: 0,
  alias: '',
  description: '',
  image: '',
  disabled: false,
  points: [],
  prices: [],
};

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  margin: {
    marginTop: theme.spacing(4),
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

function Detailed(props) {
  const classes = useStyles();
  const { state: { services }, actions: {
    addService,
    updateService,
    addPoint,
    deletePoint,
    updatePoint,
    addPriceCategory,
    deletePriceCategory,
    addPriceCategoryData,
    deletePriceCategoryData,
    updatePriceCategoryData,
  } } = useStore();
  const selectedId = props.match.params.id;

  const [service, setService] = useState({ ...defaultService });
  const [errors, setErrors] = useState({});
  const [editorState, setEditorState] = useState(
    () => {
      const state = service.description ? ContentState.createFromBlockArray(
        convertFromHTML(service.description).contentBlocks,
        convertFromHTML(service.description).entityMap
      ) : '';

      if (state) {
        return EditorState.createWithContent(state);
      } else {
        return EditorState.createEmpty();
      }
    }
  );

  const selected = services.find(item => item._id === selectedId) || {};

  const handleChange = useCallback((key, value) => {
    setService(data => ({ ...data, [key]: value }));
  }, []);

  const handleEditorChange = useCallback(editor => {
    setEditorState(editor);
  }, []);

  const handleSave = useCallback(() => {
    let error = false;

    if (!service.title || !service.alias || !service.subTitle || service.order === '' || draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>') {
      setErrors({
        title: !service.title,
        alias: !service.alias,
        subTitle: !service.subTitle,
        order: true,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>',
      });
      error = true;
    }

    if (service._id) {
      !error && updateService({
        ...service,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
      }).then(() => props.history.push('/services'));
    } else {
      !error && addService({
        ...service,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
      }).then(() => props.history.push('/services'));
    }
  }, [props.history, updateService, addService, service, editorState]);

  const handlePointSave = useCallback(data => {
    addPoint({ title: data.title, description: data.description, _id: service._id });
  }, [addPoint, service._id]);

  const handlePointUpdate = useCallback(data => {
    updatePoint({ ...data, serviceId: service._id });
  }, [updatePoint, service._id]);

  const handlePointDelete = useCallback(id => {
    deletePoint({ serviceId: service._id, _id: id })
  }, [deletePoint, service._id]);

  const handlePriceCategorySave = useCallback(data => {
    addPriceCategory({ serviceId: service._id, ...data });
  }, [addPriceCategory, service._id]);

  const handlePriceCategoryDelete = useCallback(id => {
    deletePriceCategory({ serviceId: service._id, _id: id });
  }, [deletePriceCategory, service._id]);

  const handlePriceCategoryDataAdd = useCallback(data => {
    addPriceCategoryData({ serviceId: service._id, ...data });
  }, [addPriceCategoryData, service._id]);

  const handlePriceCategoryDataDelete = useCallback((categoryId, priceId) => {
    deletePriceCategoryData({ serviceId: service._id, categoryId, priceId });
  }, [deletePriceCategoryData, service._id]);

  const handlePriceCategoryDataUpdate = useCallback(data => {
    updatePriceCategoryData({ serviceId: service._id, ...data });
  }, [updatePriceCategoryData, service._id]);

  useEffect(() => {
    if (selected._id) {
      setService(selected);
      const state = selected.description ? ContentState.createFromBlockArray(
        convertFromHTML(selected.description).contentBlocks,
        convertFromHTML(selected.description).entityMap
      ) : '';

      if (state) {
        setEditorState(EditorState.createWithContent(state));
      } else {
        setEditorState(EditorState.createEmpty());
      }
    }
  }, [selected]);

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
      >
        <Grid item xs={12} className={classes.spacer}>
          <Typography className={classes.grow} variant='h4'>{service.title || 'New service'} </Typography>
          <Button onClick={handleSave} variant="contained" color="primary">Save</Button>
        </Grid>
        <Grid item xs={12}>
          <form noValidate autoComplete="off">
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['title']}
                  helperText={errors['title'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Title"
                  onChange={e => handleChange('title', e.target.value)}
                  value={service.title}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['alias']}
                  helperText={errors['alias'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Alias"
                  onChange={e => handleChange('alias', e.target.value)}
                  value={service.alias}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['subTitle']}
                  helperText={errors['subTitle'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Sub title"
                  onChange={e => handleChange('subTitle', e.target.value)}
                  value={service.subTitle}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['order']}
                  helperText={errors['order'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Order"
                  type="number"
                  onChange={e => handleChange('order', e.target.value)}
                  value={service.order}
                />
              </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12}>
                <DraftEditor
                  editorStyle={{ height: 200, overflow: 'scroll', padding: 5, border: errors['description'] ? '1px solid red' : 'none' }}
                  editorState={editorState}
                  onEditorStateChange={handleEditorChange}
                  toolbar={defaultToolbarConfig}
                />
              </Grid>
              <Grid item xs={1} md={2} lg={3}> </Grid>
              <Grid item xs={12} md={8} lg={6}>
                <DropzoneArea
                  dropzoneClass={classes.dropzoneContainer}
                  onChange={e => handleChange('image', e[0])}
                  acceptedFiles={['image/*']}
                  initialFiles={service.image ? [`/${service.image}`] : []}
                  filesLimit={1}
                />
              </Grid>
              <Grid item xs={1} md={2} lg={3}> </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={6}>
                <PointsList
                  data={service.points}
                  onDelete={handlePointDelete}
                  onSubmit={handlePointSave}
                  onUpdate={handlePointUpdate}
                />
              </Grid>
              <Grid item xs={6}>
                <PriceList
                  data={service.prices}
                  addPriceCategory={handlePriceCategorySave}
                  deletePriceCategory={handlePriceCategoryDelete}
                  addPriceCategoryData={handlePriceCategoryDataAdd}
                  deletePriceCategoryData={handlePriceCategoryDataDelete}
                  updatePriceCategoryData={handlePriceCategoryDataUpdate}
                />
              </Grid>
            </Grid>
          </form>
        </Grid>
      </Grid>
    </div>
  );
}

export default Detailed;
